/*
=========
: 
 time: Number()
: Object( dd, hh, mm, ss)
=========
*/
export function durationFormatter(time) {
  if(!time) return{ss: 0} // time0ss = 0
  let t = time
  const ss = t % 60; // 
  t = (t - ss) / 60 // 
  if(t < 1) return {ss} // t1 ss
  const mm = t % 60 // 
  t = (t - mm) / 60 // 
  if(t < 1 ) return {mm, ss} // t1 mm, ss
  const hh = t % 24 // 
  t = (t - hh) / 24 // 
  if(t < 1) return {hh, mm, ss} // t1 hh, mm, ss
  const dd = t // t >= 1 
  return {dd, hh, mm ,ss}
}

/*
=========
(): 
 endTime: ISO8601String EX:'2021-12-08T17:36:00+08:00'
: 
=========
*/
export function getRemainTime(endTimeString) {
  let nowTime = new Date()
  let endTime = new Date(endTimeString)
  let remainTime = Math.floor((endTime - nowTime) / 1000)
  return remainTime
}


